.header {
  position: fixed;
  top: 20px;
  right: 20px;
  left: 20px;
  width: calc(100% - 40px);
  background-color: #fff;
  z-index: 100;
  height: 60px;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.25);
}

.main-nav {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 16px;
}

.main-nav__logo {
  display: flex;
  align-items: center;
}
.main-nav__logo--img {
  width: 29px;
  height: 32px;
}
.main-nav__logo--text {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-style: normal;
  font-weight: bold;
  text-align: left;
  color: #1F155B;
  margin: 0px 0px 0px 12px;
}

.main-nav__list {
  list-style-type: none;
  margin: 0 0 0 20px;
  padding: 0;
}

.main-nav__list-item {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-style: normal;
  font-weight: bold;
  text-align: left;
  color: #1F155B;
  cursor: pointer;
  display: inline;
  margin-left: 18px;
}
.main-nav__list-item:hover {
  color: #4730D2;
}

.main-nav__demo-btn {
  display: block;
  height: 100%;
  font-family: "Kumbh Sans", sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-style: normal;
  font-weight: bold;
  text-align: left;
  color: #fff;
  cursor: pointer;
  background-color: #4730D2;
  text-decoration: none;
  align-self: flex-end;
  padding: 19px 24px 17px 24px;
}

.main-nav__burger {
  display: none;
  position: absolute;
  top: 50%;
  left: 20px;
  width: 24px;
  height: 12px;
  transform: translate(0, -50%);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  z-index: 1500;
}
@media (max-width: 1359px) {
  .main-nav__burger {
    display: block;
  }
}
.main-nav__burger > span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #000;
  border-radius: 2px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}
.main-nav__burger > span:nth-child(1) {
  top: 0px;
}
.main-nav__burger > span:nth-child(2) {
  top: 6px;
}
.main-nav__burger > span:nth-child(3) {
  top: 12px;
}
.main-nav__burger.open > span {
  background-color: #000;
}
.main-nav__burger.open > span:nth-child(1) {
  top: 6px;
  transform: rotate(135deg);
}
.main-nav__burger.open > span:nth-child(2) {
  opacity: 0;
  left: -100%;
}
.main-nav__burger.open > span:nth-child(3) {
  top: 6px;
  transform: rotate(-135deg);
}
@media (min-width: 1921px) {
  .main-nav__burger {
    top: 110px;
  }
}

